@import url(https://fonts.googleapis.com/css2?family=Baloo+Chettan+2:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar-container{width:100%;background-color:#ffecc1;position:fixed;top:0;left:0;z-index:999;font-family:'Baloo Chettan 2', cursive;font-size:20px}nav.navbar-light{background-color:#ffecc1;max-width:1333px;margin:0 auto;white-space:nowrap}nav.navbar-light .navbar-toggler{border:none;padding-bottom:8px;padding-right:0px}nav.navbar-light .navbar-toggler:focus{outline:none}nav.navbar-light .navbar-toggler-icon{background-image:url("data:image/svg+xml,%3csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='rgba(66, 63, 56, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e")}nav.navbar-light .dropdown-item{cursor:pointer}.navbar-underline{height:2px;width:100%;background:#b83919}.logo-text{font-size:26px;margin:0px 0px 3px 10px}.dark-gray{color:#423f38}.red{color:#b83919}.links{padding-right:16px}.btn{border:none;border-radius:0px 5px 5px 0px;font-size:20px;padding:0px 10px 10px 10px;height:32px;width:40px;color:#866820;background-color:#faf9f8}.login-button-mobile{padding-bottom:6px;padding-right:8px}.user-button-mobile{padding-bottom:6px}.desktop-links{display:flex;width:100%;justify-content:center;padding-right:200px}@media (min-width: 991px){.nav-link{padding-left:70px;padding-right:70px}}

.footer-overline{height:2px;width:100%;background:#b83919}.footer-container{width:100%;background-color:#ffecc1;padding:30px}.footer-container a{color:#423f38}.footer-flexbox{display:flex;flex-wrap:wrap;justify-content:space-between;font-size:13px;background-color:#ffecc1;max-width:1333px;margin:0 auto;color:#423f38}.footer-copyright{width:100%;text-align:center}@media (max-width: 991px){.footer-flexbox{justify-content:center}}

.btn-primary{border:none;border-radius:5px;background:#b83919;padding:15px;box-shadow:0px 0px 10px 0px rgba(0,0,0,0.5);transition:.5s}.btn-primary a{color:#faf9f8}.btn-primary:hover{background:#e85b38;box-shadow:0px 0px 10px 0px rgba(0,0,0,0.75)}.btn-filter{border:1px solid #b8b3a4;border-radius:5px;color:#706c61;font-size:14px;background:#efedea;padding:2px 7px 2px 7px;margin:3px 6px 3px 0px;min-width:50px;text-align:center;cursor:pointer;transition:.3s}.btn-filter--checked{border:1px solid #b83919;color:#b83919}

.index-wrapper{flex:1 1 auto;display:flex;flex-flow:column;justify-content:center;align-items:center;width:100%;min-height:100%;padding:0px 15px 0px 15px;text-align:center;background:#fffaef}.index-wrapper .content{width:100%;max-width:1333px;text-align:right;padding-right:50px}.index-wrapper .p1{font-size:40px;color:#423f38}.index-wrapper .p2{font-size:28px;color:#706c61;margin-bottom:30px}@media (max-width: 991px){.index-wrapper .content{text-align:center;padding-right:0px}.index-wrapper .p1{font-size:26px}.index-wrapper .p2{font-size:20px}}

.grid-container{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:0.4fr 1.6fr 1fr;grid-gap:1px 1px;gap:1px 1px;grid-template-areas:"title title photo photo" "text text photo photo" "text text photo photo";padding-left:200px}.photo{grid-area:photo;padding-top:100px;padding-left:50px;padding-right:50px}.photo .three{padding:60px 10px 10px 10px}.title{font-size:50px;padding-top:150px}.text{grid-area:text;padding-top:40px;padding-right:50px;opacity:0.9;text-align:justify}@media (max-width: 991px){.photo .three{display:none}.title{text-align:center;font-size:25px}.text{text-align:left;font-size:15px}}

.contact-wrapper{flex:1 1 auto;display:flex;flex-flow:column;width:100%;min-height:100%;padding:0px 15px 0px 15px;padding-left:200px;font-size:30px}.contact-wrapper .contact-title{font-size:50px;padding-top:150px}.contact-wrapper .contact-pizza-name{padding-top:80px;font-size:40px}.contact-wrapper .contact-address{padding-top:50px}.contact-wrapper .contact-contact{padding-top:50px}

body{font-family:'Roboto', sans-serif;font-family:'Baloo Chettan 2', cursive;font-size:18px;color:#423f38}a{color:#423f38;transition:.5s}a:hover{color:#b83919;text-decoration:none}.wrapper{display:flex;flex-flow:column;min-height:100vh;background-color:#fffaef}.App{width:100%}

